.form__closer {
  position: absolute;
  z-index: 3;
  top: 50px;
  right: 50px;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: transparent;
  border: 0;
  overflow: visible;
  cursor: pointer; }
  @media (max-width: 1023px) {
    .form__closer {
      right: 77px; } }
  @media (max-width: 768px) {
    .form__closer {
      top: 38px;
      right: 8px; } }
  .form__closer::after, .form__closer::before {
    content: "";
    position: absolute;
    top: 14px;
    right: 3px;
    width: 24px;
    height: 2px;
    background-color: #1f1e25; }
    @media (max-width: 768px) {
      .form__closer::after, .form__closer::before {
        right: 5px;
        width: 18px; } }
  .form__closer::before {
    transform: rotate(45deg); }
  .form__closer::after {
    transform: rotate(-45deg); }
  .form__closer:focus, .form__closer:hover {
    outline: none;
    box-shadow: 0 0 10px 3px #2c36f2; }

.popup-login {
  display: none;
  position: fixed;
  z-index: 100;
  top: 50%;
  left: 50%; }

.popup-login--show {
  display: block; }

.popup-login__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 110;
  background-color: transparent; }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }
