html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 100%;
  margin: 0;
  font-size: 16px;
  line-height: 19px;
  font-family: $font-base;
  color: $color-default;

  @media ($tablet) {
    font-size: 14px;
    line-height: 16px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  text-decoration-skip-ink: none;
  transition-timing-function: ease-in-out;
  transition-duration: 0.3s;
  transition-property: color, opacity, background-color, border-color, box-shadow;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  min-width: 1024px;
  max-width: 1210px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;

  @media ($tablet) {
    min-width: 678px;
    padding-right: 0;
    padding-left: 0;
  }

  @media ($mobile) {
    min-width: 290px;
    max-width: 768px;
  }
}
